* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

body {
    background: linear-gradient(135deg, #1a2a6c, #b21f1f, #fdbb2d);
    background-attachment: fixed;
    color: #333;
    padding: 20px;
    min-height: 100vh;
}

.container {
    max-width: 1400px;
    margin: 0 auto;
    padding: 20px;
}

.app-card {
    background: rgba(255, 255, 255, 0.95);
    border-radius: 15px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
    overflow: hidden;
}

header {
    background: linear-gradient(to right, #2c3e50, #4a6491);
    color: white;
    padding: 25px 30px;
    text-align: center;
}

h1 {
    font-size: 2.5rem;
    margin-bottom: 10px;
    text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.3);
}

.subtitle {
    font-size: 1.2rem;
    opacity: 0.9;
    margin-bottom: 15px;
}

.tech-stack {
    display: flex;
    justify-content: center;
    gap: 15px;
    flex-wrap: wrap;
    margin-top: 15px;
}

.tech-item {
    background: rgba(255, 255, 255, 0.2);
    padding: 5px 15px;
    border-radius: 20px;
    font-size: 0.9rem;
}

.content {
    padding: 30px;
}

.filters-section {
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
    margin-bottom: 25px;
    padding: 20px;
    background: #f8f9fa;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
}

.filter-group {
    flex: 1;
    min-width: 200px;
}

.filter-group label {
    display: block;
    margin-bottom: 8px;
    font-weight: 600;
    color: #2c3e50;
}

.filter-group input, .filter-group select {
    width: 100%;
    padding: 10px 15px;
    border: 1px solid #ddd;
    border-radius: 8px;
    font-size: 1rem;
    transition: all 0.3s ease;
}

.filter-group input:focus, .filter-group select:focus {
    border-color: #3498db;
    box-shadow: 0 0 0 3px rgba(52, 152, 219, 0.2);
    outline: none;
}

.table-container {
    overflow-x: auto;
    border-radius: 10px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.08);
    background: white;
}

table {
    width: 100%;
    border-collapse: collapse;
    min-width: 1000px;
}

th {
    background: linear-gradient(to bottom, #4a6fa5, #2c3e50);
    color: white;
    font-weight: 600;
    text-align: left;
    padding: 15px 20px;
    position: relative;
    cursor: pointer;
    user-select: none;
}

th:hover {
    background: linear-gradient(to bottom, #3a5a85, #1c2e40);
}

.sort-indicator {
    margin-left: 5px;
    font-size: 0.9rem;
}

.resize-handle {
    position: absolute;
    top: 0;
    right: 0;
    width: 5px;
    height: 100%;
    background: rgba(255, 255, 255, 0.3);
    cursor: col-resize;
}

td {
    padding: 12px 20px;
    border-bottom: 1px solid #eee;
}

tr:nth-child(even) {
    background-color: #f9f9f9;
}

tr:hover {
    background-color: #f1f7ff;
}

.pagination {
    display: flex;
    justify-content: center;
    margin-top: 25px;
    gap: 10px;
}

.pagination button {
    padding: 8px 16px;
    background: #3498db;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-weight: 600;
    transition: all 0.3s ease;
}

.pagination button:hover {
    background: #2980b9;
    transform: translateY(-2px);
}

.pagination button:disabled {
    background: #bdc3c7;
    cursor: not-allowed;
    transform: none;
}

.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

.modal-content {
    background: white;
    border-radius: 15px;
    width: 90%;
    max-width: 700px;
    max-height: 90vh;
    overflow-y: auto;
    box-shadow: 0 15px 40px rgba(0, 0, 0, 0.3);
    position: relative;
}

.modal-header {
    background: linear-gradient(to right, #2c3e50, #4a6491);
    color: white;
    padding: 20px;
    border-radius: 15px 15px 0 0;
}

.modal-body {
    padding: 25px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
}

.user-avatar {
    grid-column: 1 / -1;
    text-align: center;
    margin-bottom: 15px;
}

.user-avatar img {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    border: 5px solid #f1f1f1;
    object-fit: cover;
}

.user-info {
    padding: 10px;
}

.user-info h3 {
    margin-bottom: 10px;
    color: #2c3e50;
    border-bottom: 2px solid #3498db;
    padding-bottom: 5px;
}

.info-row {
    display: flex;
    margin-bottom: 8px;
}

.info-label {
    font-weight: 600;
    min-width: 120px;
    color: #2c3e50;
}

.close-btn {
    position: absolute;
    top: 15px;
    right: 20px;
    background: none;
    border: none;
    color: white;
    font-size: 1.8rem;
    cursor: pointer;
    transition: transform 0.3s ease;
}

.close-btn:hover {
    transform: scale(1.2);
}

.loading {
    text-align: center;
    padding: 40px;
    font-size: 1.2rem;
    color: #3498db;
}

.error {
    text-align: center;
    padding: 40px;
    color: #e74c3c;
    font-size: 1.1rem;
    background: #ffebee;
    border-radius: 10px;
    margin: 20px;
}

@media (max-width: 768px) {
    .modal-body {
        grid-template-columns: 1fr;
    }
    
    .content {
        padding: 15px;
    }
    
    h1 {
        font-size: 2rem;
    }
}